$break-tablet: 769px;
$break-large-tablet: 1360px;
$break-small-desktop: 1500px;

.is_6_tablet {
  @media screen and (min-width: $break-tablet) and (max-width: $break-large-tablet) {
    width: 50% !important;
  }

  @media screen and (min-width: $break-large-tablet) and (max-width: $break-small-desktop) {
    width: 33.3% !important;
  }
}

.loader_wrapper {
  height: 50vh;
  width: 70vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.quantity_input {
  border: none;
  width: 2rem;
  padding: 0;
  text-align: center;
  font-size: 1rem;
}

.elipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.product_card {
  background-color: transparent;
  padding: 3rem 2rem;
  box-shadow: 0px 0px 3px 1px rgba(246, 247, 249, 1);
}

.product_card_text {
  justify-content: space-between;

  .discounted_price {
    color: #282828;
    font-weight: 600;
  }
}
